/**
 * Gallerya layout and styling.
 *
 * 1. Take the available width, subtract the necessary gaps between thumbnails
 *    and divide the remaining space evenly among thumbnails.
 */

$screen-xs-min: 480px;
$screen-sm-min: 768px;
$screen-md-min: 992px;
$screen-lg-min: 1200px;

$color-light: #fff;
$color-dark: #000;
$color-primary: #e0e2e3;
$color-secondary: #c4c4c4;

.gallerya {

  &--slider,
  &--product-variation-slider,
  &--grid {

    > ul {
      &:after {
        // Prevent this from being overriden by clearfixes through using !important.
        content: 'flickity' !important;
        display: none !important;
      }
    }
  }

  &--product-variation-slider,
  &--slider {

    @media screen and (min-width: $screen-sm-min) {
      margin-right: auto;
      margin-left: auto;
    }

    clear: both;

    > ul {
      list-style: none;
      padding: 0;
      margin: 0;

      li {
        width: 100%;
      }
    }


    .gallerya__image {
      @media screen and (min-width: $screen-sm-min) {
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  &--slider {
    margin-top: 20px;
    margin-bottom: 40px;

    @media screen and (min-width: $screen-sm-min) {
      margin-top: 50px;
      margin-bottom: 75px;
    }

    > ul {
      li {
        top: 50%;
        transform: translateY(-50%);
      }
    }

    .gallerya__image {
      margin-bottom: 30px;

      @media screen and (min-width: $screen-sm-min) {
        width: 75%;
      }
    }

    li {
      width: auto;
      padding: 0 5px;
    }

    .gallerya__image {
      width: 100%;
      margin: initial;
      display: block;
      max-height: 90px;

      img {
        width: auto;
        display: block;
        max-width: none;
        max-height: inherit;
      }
    }
  }

  .flickity-prev-next-button {
    top: 50%;
    width: 28px;
    height: 28px;
    margin: 0;
    padding: 0;
    outline: none;
    transform: translateY(-50%);
    background: $color-primary;

    &:hover {
      background: $color-secondary;
    }

    &.previous {
      left: 4px;
    }

    &.next {
      right: 4px;
    }

    &[disabled] {
      opacity: .5;
    }

    .arrow {
      fill: #fff;
    }
  }

  &--product-variation-slider {
    width: 100%;

    // Hide all slides but first before Flickity is initialized to prevent reflows.
    li:not(:first-of-type) {
      display: none;
    }
    .flickity-enabled {
      li:not(:first-of-type) {
        display: block;
      }
    }

    .flickity {
      white-space: nowrap;
      overflow: visible;

      li {
        display: inline-block;
      }

      &-prev-next-button {
        @media screen and (min-width: $screen-sm-min) {
          &.previous {
            left: 10px;
          }

          &.next {
            right: 10px;
          }
        }
      }
    }
  }

  &--grid {
    @extend .gallerya--slider;
    @media screen and (min-width: $screen-sm-min) {
      ul {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        padding: 0;
        margin: 0;
        margin-left: -20px;

        &:after {
          content: '';
        }

        li {
          list-style: inherit;
          flex-basis: calc(100%/2 - 20px);
          margin-left: 20px;
          transform: none;
        }
      }

      .gallerya__image {
        margin: 0;
        margin-bottom: 20px;
        overflow: hidden;
        width: auto;

        figcaption {
          transform: translateY(100%);
          transition: all .2s ease-out;
        }

        &:hover {
          figcaption {
            transform: translateY(0);
          }
        }
      }
    }
  }

  &__image {
    position: relative;

    a {
      display: block;
      text-align: center;
    }

    img {
      width: auto;
      max-height: 645px;
      vertical-align: middle;
    }

    figcaption {
      width: 100%;
      display: block;
      padding: 15px;
      color: $color-secondary;
      font-size: 14px;
      line-height: 1.4;
      background: $color-light;

      @media screen and (min-width: $screen-sm-min) {
        position: absolute;
        left: 0;
        bottom: 0;
        padding: 10% 15px 15px;
        background: linear-gradient(to top, $color-dark 0%, transparent 100%);
        color: $color-primary;
      }
    }
  }

  &__image-group {
    display: flex;
    flex-wrap: wrap;
    transform: translateX(-5px);
    @media screen and (min-width: $screen-sm-min) {
      width: 75%;
      margin-left: auto;
      margin-right: auto;
    }

    .gallerya__image {
      margin: 0;
      width: auto;
      flex: 1 0 30%;
      margin-bottom: 5px;
      padding-left: 5px;
      max-width: calc(100% / 2);
      @media screen and (min-width: $screen-sm-min) {
        max-width: calc(100% / 3);
      }
    }
  }

  .flickity-page-dots {
    margin: 0;
    padding: 0;
    outline: none;
  }

  .flickity-page-dots .dot {
    background: $color-primary;

    &:hover {
      background: $color-secondary;
    }
  }

  &__count {
    font-size: 0.875em;
    text-align: center;
  }
}

.lg-outer .lg-item {
  background-image: url("../images/loading.gif");
}

.is-currently-selected {
  .gallerya__image {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      border: 2px solid $color-primary;
    }
  }
}

// Single Product Gallery Bullet Nav
.flex-control-paging {
  display: block;
  position: relative;
  width: 100%;
  height: 19px;
  margin: 16px 0 0px;
  padding: 0;
  list-style: none;
  text-align: center;

  li {
    position: relative;
    display: inline-block;
    cursor: pointer;
    margin: 0;
    padding: .25rem;
    line-height: 0;
    vertical-align: top;

    a {
      position: relative;
      display: inline-block;
      height: 11px !important;
      width: 11px !important;
      margin: 0;
      padding: 0;
      border-radius: 50%;
      border: 2px solid $color-secondary;
      background: 0 0;
      background-color: transparent;
      outline: 0;
      line-height: 0;
      vertical-align: top;
      font-size: 0;
      text-indent: -99999px;
      color: transparent;
      cursor: pointer;

      &:focus,
      &:hover {
        outline: 0;

        &:before {
          opacity: .5;
        }
      }

      &:hover {
        &:before {
          background-color: $color-secondary !important;
        }
        &:after {
          content: "";
          display: none !important;
        }
      }

      &:before {
        content: "";
        position: absolute;
        display: block;
        width: 11px;
        height: 11px;
        top: -2px;
        left: -2px;
        border-radius: 50%;
        color: $color-secondary;
        transition: background .1s ease-out;
      }

      &.flex-active {
        background-color: $color-secondary;
      }
    }
  }
}

// Flickity WooCommerce Compatibility
.woocommerce {
  .product {
    .flex-control-thumbs {
      --thumbnail-count: 6;
      --thumbnail-spacing-unit: 9px;
      --thumbnail-slider-margin: 12px;

      &.flickity {
        display: block;
        white-space: nowrap;
        overflow: visible !important;
        margin-left: var(--thumbnail-slider-margin) !important;
        margin-right: var(--thumbnail-slider-margin) !important;

        li {
          float: none !important;
          display: inline-block;
          margin-right: var(--thumbnail-spacing-unit) !important;
          margin-bottom: 0;
          width: calc((100% - (var(--thumbnail-spacing-unit) * (var(--thumbnail-count) - 1))) / var(--thumbnail-count)) !important; // [1]
        }

        img {
          opacity: 0.7;
          border: 1px solid transparent;

          &:hover,
          &.flex-active {
            opacity: 1;
            border: 1px solid rgba(0,0,0,0.5);
          }
        }

        &:after {
          content: " ";
          display: table;
          clear: both;
        }
      }
    }
  }
}
